import React from 'react';
import { Map, MouseTool } from 'react-amap';

const layerStyle = {
  padding: '10px',
  background: '#fff',
  border: '1px solid #ddd',
  borderRadius: '4px',
  position: 'absolute',
  top: '10px',
  left: '10px'
};

class Amap extends React.Component{
  constructor(){
    super();
    const self =this;
    this.state = {
      what: '点击下方按钮开始绘制'
    };
    this.toolEvents = {
      created: (tool) => {
        console.log(tool)
        self.tool = tool;
      },
      draw({obj}) {
        console.log("props : ", obj.getPosition())
        self.drawWhat(obj);
      }
    }
    this.mapPlugins = ['ToolBar'];
    this.mapCenter = {longitude: 120, latitude: 35};
  }

  

  drawWhat(obj) {
    let text = '';
    switch(obj.CLASS_NAME) {
      case 'AMap.Marker':
       text = `你绘制了一个标记，坐标位置是 {${obj.getPosition()}}`;
       break;
    
      default:
        text = '';
    }
    this.setState({
      what: text
    });
  }
  
  drawMarker(){
    if (this.tool){
      this.tool.marker();
      this.setState({
        what: '准备绘制坐标点'
      });
    }
  }

  
  
  close(){
    if (this.tool){
      this.tool.close();
    }
    this.setState({
      what: '关闭了鼠标工具'
    });
  }
  
  render(){
    return <div>
      <div style={{width: '100%', height: 370}}>
        <Map 
        amapkey={'79f4e464d0173234eee9dc574e89edd9'}
          plugins={this.mapPlugins}
          center={this.mapCenter}
        >
          <MouseTool events={this.toolEvents}/>
          <div style={layerStyle}>{this.state.what}</div>
        </Map>
       </div>
       <button onClick={()=>{this.drawMarker()}}>Draw Marker</button>
       <button onClick={()=>{this.close()}}>Close</button>
     </div>
  }
}

export default Amap;