import React, { useEffect, memo } from "react";
import ReactDOM from "react-dom";
import Header from "../../partials/Header";
import SideBar from "../Partials/SideBar";
import Amap from "./Amap";
import {
  GetMe,
  Patient_List_All,
} from "../../../../entity/Redux/Doctors/Action";
import { GetToken } from "../../../../services/Authentication/GetToken";
import { useSelector, useDispatch } from "react-redux";
import M from "materialize-css";

const style = (theme) => ({
  selectOpt: {
    borderBottom: "1px solid #fff !important",
  },
});
export default memo(() => {
  const dispatch = useDispatch();

  const GetMeDetail = useSelector((state) => state.Doctor);

  console.log("vsdvsdv", GetMeDetail);

  var elems = document.querySelectorAll("select");
  M.FormSelect.init(elems);
  useEffect(() => {
    dispatch(GetMe());
    setTimeout(() => {
      dispatch(Patient_List_All());
    }, 500);
    // ReactDOM.render(<Amap />, document.getElementById("mappppp"))
    // ;
  }, []);

  const chekType = (types) => {
    var typeArr = types.split(".");
    var type = typeArr[typeArr.length - 1];
    if (type == "png" || type == "jpg" || type == "jpeg") {
      return "img";
    } else {
      return "file";
    }
  };

  // console.log("dataaaaaa : " , GetMeDetail)

  const renderContent = () => {
    return (
      <section id="content">
        <div className="container">
          <div id="card-stats">
            <div className="row mt-1">
              <div className="col s12 m6 l4">
                <div className="card gradient-45deg-amber-amber gradient-shadow min-height-100 white-text">
                  <div id="back_icon">
                    <i className="material-icons">hotel</i>
                  </div>
                  <div className="padding-4">
                    <div className="col s7 m7">
                      <i className="material-icons background-round mt-5">
                        hotel
                      </i>
                    </div>
                    <div className="col s5 m5 right-align">
                      <h5 className="mb-0">10</h5>
                      {/* <h5 className="mb-0">{GetMeDetail.allPatients ? GetMeDetail.allPatients.data.data.length : ''}</h5> */}
                      <p className="no-margin">Old Patient</p>
                    </div>
                  </div>
                </div>
              </div>

              <div className="col s12 m6 l4">
                <div className="card gradient-45deg-red-pink gradient-shadow  min-height-100 white-text">
                  <div id="back_icon">
                    <i className="material-icons">hotel</i>
                  </div>
                  <div className="padding-4">
                    <div className="col s7 m7">
                      <i className="material-icons background-round mt-5">
                        hotel
                      </i>
                    </div>
                    <div className="col s5 m5 right-align">
                      <h5 className="mb-0">10</h5>
                      {/* <h5 className="mb-0">{GetMeDetail.allPatients ? GetMeDetail.allPatients.data.data.currents.length : ''}</h5> */}
                      <p className="no-margin">New Patient</p>
                    </div>
                  </div>
                </div>
              </div>

              <div className="col s12 m6 l4">
                <div className="card gradient-45deg-green-teal gradient-shadow min-height-100 white-text">
                  <div id="back_icon">
                    <i className="material-icons">playlist_add_check</i>
                  </div>
                  <div className="padding-4">
                    <div className="col s7 m7">
                      <i className="material-icons background-round mt-5">
                        playlist_add_check
                      </i>
                    </div>
                    <div className="col s5 m5 right-align">
                      <h5 className="mb-0">70</h5>
                      <p className="no-margin">Tests</p>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>

          <div
            id="work-collections"
            style={{
              marginBottom: 30,
            }}
          >
            <div className="row">
              <div className="col s12">
                <div className="doctor_setting">
                  <a
                    className="btn-floating green darken-4 waves-effect"
                    style={{ position: "fixed", right: "30px", bottom: "20px" }}
                  >
                    <i className="material-icons">update</i>
                  </a>

                  <div
                    className="col s12"
                    style={{ marginBottom: "-30px", marginLeft: "10px" }}
                  >
                    <a className="custom_a_title">Your Profile Data</a>
                  </div>

                  <div className="col s12 l6">
                    <div className="input-field col s12">
                      <p style={{ color: "#ccc" }}>Full Name</p>

                      <div id="input_back">
                        <input
                          className="validate invalid"
                          placeholder="enter your name ..."
                          value={
                            GetMeDetail.GetMe
                              ? GetMeDetail.GetMe.data.data.fullName
                              : "loading ..."
                          }
                        />
                      </div>
                    </div>
                  </div>

                  <div className="col s12 l6">
                    <div className="input-field col s12">
                      <p style={{ color: "#ccc" }}>Commission Rate</p>

                      <div id="input_back">
                        <input
                          className="validate invalid"
                          placeholder="commission rate"
                          //    value={GetMeDetail.GetMe ? GetMeDetail.GetMe.data.data.doctor.commissionRate == "" ? 0 : GetMeDetail.GetMe.data.data.doctor.commissionRate : 'loading ...'}
                        />
                      </div>
                    </div>
                  </div>

                  <div className="col s12 l6">
                    <div className="input-field col s12">
                      <p style={{ color: "#ccc" }}>Phone Number</p>

                      <div id="input_back">
                        <span className={"disabledd"}></span>
                        <input
                          className="validate invalid"
                          placeholder="phoneNumber"
                          value={
                            GetMeDetail.GetMe
                              ? GetMeDetail.GetMe.data.data.phoneNumber
                              : "loading ..."
                          }
                        />
                      </div>
                    </div>
                  </div>

                  <div className={"col s12 l6"}>
                    <div className="input-field col s6">
                      <p style={{ color: "#ccc" }}>Your Grade</p>

                      <div id="input_back">
                        <span className={"disabledd"}></span>
                        <input
                          className="validate invalid"
                          placeholder="grade"
                          value={
                            GetMeDetail.GetMe
                              ? GetMeDetail.GetMe.data.data.doctor.grade == ""
                                ? "not set untill"
                                : GetMeDetail.GetMe.data.data.doctor.grade
                              : "loading ..."
                          }
                        />
                      </div>
                    </div>

                    <div className="input-field col s6">
                      <p style={{ color: "#ccc" }}>Status</p>

                      <div id="input_back">
                        <span className={"disabledd"}></span>
                        <input
                          className="validate invalid"
                          placeholder=""
                          value={
                            GetMeDetail.GetMe
                              ? GetMeDetail.GetMe.data.data.doctor
                                  .isConfirmedByAdmin == false
                                ? "Disabled"
                                : "Enabled"
                              : "loading ..."
                          }
                        />
                      </div>
                    </div>
                  </div>

                  <div className="input-field col s12 l6">
                    <p style={{ color: "#ccc" }}>Education Degree</p>

                    <select>
                      <option
                        value="0"
                        disabled
                        selected={
                          GetMeDetail.GetMe.data.data.doctor.educationDegree ==
                          ""
                            ? true
                            : false
                        }
                      >
                        Doctor Education Degre
                      </option>
                      <option
                        selected={
                          GetMeDetail.GetMe.data.data.doctor.educationDegree ==
                          "Diploma"
                            ? true
                            : false
                        }
                        value="Diploma"
                      >
                        Diploma
                      </option>
                      <option
                        selected={
                          GetMeDetail.GetMe.data.data.doctor.educationDegree ==
                          "Masters"
                            ? true
                            : false
                        }
                        value="Masters"
                      >
                        Masters
                      </option>
                      <option
                        selected={
                          GetMeDetail.GetMe.data.data.doctor.educationDegree ==
                          "Phd"
                            ? true
                            : false
                        }
                        value="Phd"
                      >
                        PHD
                      </option>
                    </select>
                  </div>

                  <div className="input-field col s12 l6">
                    <p style={{ color: "#ccc" }}>Education Field</p>

                    <select>
                      <option
                        value="0"
                        disabled
                        selected={
                          GetMeDetail.GetMe.data.data.doctor.educationField ==
                          ""
                            ? true
                            : false
                        }
                      >
                        Doctor Education Filed
                      </option>
                      <option
                        selected={
                          GetMeDetail.GetMe.data.data.doctor.educationField ==
                          "Diploma"
                            ? true
                            : false
                        }
                        value="Diploma"
                      >
                        Diploma
                      </option>
                      <option
                        selected={
                          GetMeDetail.GetMe.data.data.doctor.educationField ==
                          "Masters"
                            ? true
                            : false
                        }
                        value="Masters"
                      >
                        Masters
                      </option>
                      <option
                        selected={
                          GetMeDetail.GetMe.data.data.doctor.educationField ==
                          "Phd"
                            ? true
                            : false
                        }
                        value="Phd"
                      >
                        PHD
                      </option>
                    </select>
                  </div>

                  <div className={"col s12"}></div>

                  <div className="col s12">
                    <div className="col s12">
                      <a className="custom_a_title">Document uploaded</a>
                    </div>

                    {GetMeDetail.GetMe.data.data.doctor.attachment.map(
                      (item) => {
                        return (
                          <div className="doc_doctor">
                            {chekType(item) == "img" ? (
                              <img
                                src={item}
                                style={{ width: 60, marginTop: 20 }}
                              />
                            ) : (
                              <a href={item}>
                                <i className="material-icons">picture_as_pdf</i>
                              </a>
                            )}
                          </div>
                        );
                      }
                    )}
                  </div>

                  <div className="col s12">
                    <div className="col s12">
                      <a className="custom_a_title">Your position on the map</a>
                    </div>

                    <div id="mappppp"></div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    );
  };

  const renders = () => {
    return (
      <div>
        <Header />
        <div id="main">
          <div className="wrapper">
            <SideBar />

            <div id="back_shado"></div>

            {GetMeDetail.GetMe.data ? renderContent() : "<p>please wait ...</p>"}
          </div>
        </div>
      </div>
    );
  };

  const loading = () => {
    return <div>please wait</div>;
  };

  return GetMeDetail.GetMe ? renders() : loading();
});
